/*
 * Page Action (Chrome feature)
 */
import authSuccessTemplate from 'templates/page_action/auth-success.hbs'
import authFailureTemplate from 'templates/page_action/auth-failure.hbs'
import syncItemsTemplate from 'templates/popup/sync/sync-items.hbs'
import $ from 'jquery'
import { urlBase } from 'common/modalyst_api'
import 'styles.scss'
import { getStoreNameByUrl } from 'utils'

function getContent (username, currentUrl) {
  const externalStore = getStoreNameByUrl(currentUrl)

  if (!username) return authFailureTemplate({ modalyst_root: urlBase, external_store: externalStore })

  if (currentUrl.pathname.startsWith('/transaction')) {
    return syncItemsTemplate({ modalyst_root: urlBase, external_store: externalStore })
  }

  return authSuccessTemplate({ modalyst_root: urlBase, external_store: externalStore })
}

function syncItems (tabId) {
  chrome.tabs.sendMessage(tabId, { topic: 'start-sync' })
  window.close()
}

chrome.storage.local.get('username', ({ username }) => {
  chrome.tabs.query({ active: true, lastFocusedWindow: true }, function (tabs) {
    const currentUrl = new URL(tabs[0].url)
    const tabId = tabs[0].id

    const content = getContent(username, currentUrl)
    const authenticated = !!username

    $('.popup-head').toggleClass('is-signed-in', authenticated)
    if (username) $('[data-username]').text(username)
    $('.popup-content').html(content)

    $('#sync-items-button').click(() => syncItems(tabId))
  })
})
